<script>
import { ref, computed, onMounted } from 'vue'
import dayjs from 'dayjs'
import { http } from '@/api/http';

export default {
  setup() {
    // Áä∂ÊÄÅÁÆ°ÁêÜ
    const activeTab = ref('plans')
    const isLoading = ref(false)
    const plans = ref([])
    const guides = ref([])
    const sortType = ref('newest') // Êñ∞Â¢ûÊéíÂ∫èÁä∂ÊÄÅ

    // ËÆ°ÁÆóÂ±ûÊÄßÔºàÁßªÈô§ÂüéÂ∏ÇËøáÊª§Ôºâ
    const filteredPlans = computed(() => {
      return plans.value.filter(plan => 
        new Date(plan.date) >= new Date(new Date().setHours(0, 0, 0, 0))
      )
    })

    const filteredGuides = computed(() => guides.value)

    // Êñ∞Â¢ûÊéíÂ∫èÂäüËÉΩ
    const sortedPlans = computed(() => {
      return [...filteredPlans.value].sort((a, b) => 
        sortType.value === 'newest' 
          ? new Date(b.date) - new Date(a.date)
          : a.title.localeCompare(b.title)
      )
    })

    const sortedGuides = computed(() => {
      return [...filteredGuides.value].sort((a, b) =>
        sortType.value === 'price' 
          ? a.price - b.price
          : new Date(b.createdAt) - new Date(a.createdAt)
      )
    })

    // Êï∞ÊçÆËé∑ÂèñÊñπÊ≥ï
    const fetchData = async (url) => {
      try {
        isLoading.value = true
        const response = await http.get(url)
        return response.data || []
      } catch (error) {
        console.error('Êï∞ÊçÆËé∑ÂèñÂ§±Ë¥•:', error)
        return []
      } finally {
        isLoading.value = false
      }
    }

    // ÂàùÂßãÂåñÂä†ËΩΩ
    onMounted(() => {
      Promise.all([
        fetchData('/travel-plans').then(data => plans.value = data),
        fetchData('/guide-services').then(data => guides.value = data)
      ])
    })

    return {
      // Áä∂ÊÄÅ
      activeTab,
      isLoading,
      plans,
      guides,
      sortType,

      // ËÆ°ÁÆóÂ±ûÊÄß
      sortedPlans,
      sortedGuides,

      // ÊñπÊ≥ï
      formatDate: date => dayjs(date).format('YYYY-MM-DD'),
      startChat: item => {
        uni.navigateTo({
          url: `/pages/chat/chat?userId=${item.userId}&title=${encodeURIComponent(item.title)}`
        })
      }
    }
  }
}
</script>

